.about{

    display: flex;
    justify-content: space-between;
    align-items: center;
  
    margin-top: 1rem;
   

}

.about .sec1{
   
    padding: 2rem;
    display: block;
    align-items: center;
    justify-content: center;
    padding-left: 7rem;
    padding-top: 5rem;
   
}

.about .sec1 h3{
    font-family: Poppins,sans-serif;
    font-size: 3.5rem;
    font-weight: 700;
    letter-spacing: -.05em;
    line-height: 1.15;
    color: rgb(19 15 73);
}


.about .sec1 .bluecolor{
    color: rgb(52 161 205);
}


.about .sec1 p{

    font-family: 'PT Mono', monospace;
    line-height: 1.5;
    align-self: center;
    margin-top: 1.5rem;
}



.about .sec1 button{
    background-color: rgb(52 161 205);
    border-radius: 5px;
    color: white;
    border: none;
    font-family: 'PT Mono', monospace;
    padding-top: 18px;
    padding-bottom: 18px;
    padding-left: 30px;
    padding-right: 30px;
    cursor:pointer;
    margin-top: 2rem;
   
   

  }


  .about .sec1 .btn-color{
    background-color: rgb(19 15 73);
   
}


.about .sec1 .btn2{

    margin-left: 5rem;
}





.about .sec2{
    box-shadow: rgb(85, 91, 255) 0px 0px 0px 3px, rgb(31, 193, 27) 0px 0px 0px 6px, rgb(255, 217, 19) 0px 0px 0px 9px, rgb(255, 156, 85) 0px 0px 0px 12px, rgb(255, 85, 85) 0px 0px 0px 15px;
   
    
    margin-top: 2em;
    overflow: hidden;
    height: fit-content;
    width: fit-content;
}

.about .sec2 img{

    width: 100%;
    height: 100%;
    object-fit: contain;
   

}






@media screen and (max-width:700px) {



    .about .sec1{
      
        padding: 2rem;
        display: block;
        align-items: center;
        justify-content: center;
        padding-left: 2rem;
        padding-top: 2rem;
       
    }


  


    .about{

        display: block;
       
    }


    .about .sec1 h3{
        font-family: Poppins,sans-serif;
        margin-left: 2rem;
        font-size: 2.5rem;
        font-weight: 700;
        letter-spacing: -.05em;
        line-height: 1.15;
        color: rgb(19 15 73);
    }


    .about .sec1 p{

        font-family: 'PT Mono', monospace;
        line-height: 1.5;
        align-self: center;
        margin-top: 1rem;
        margin-left: 2rem;
    }


    .about .sec1 button{
       
        border-radius: 5px;
        color: white;
        border: none;
        font-family: 'PT Mono', monospace;
        padding-top: 18px;
        padding-bottom: 18px;
        padding-left: 20px;
        padding-right: 20px;
        cursor:pointer;
        margin-left: 2rem;
        
        
      
       
      }
    
      .about .sec1 .btn2{

        margin-left: 20px;
    }
    
}