.header1{
   
   
    width: 100%;
    height: 3.5rem;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
   
    

}

.logo{
    color:rgb(19 15 73);
    font-family: 'Itim', cursive;
    font-size: 2.5rem;
    font-weight: 600;
    cursor: pointer;
    margin-left: 5rem;
   
 

    
}


.list{
    list-style-type: none;
  /*  background-color: blueviolet; */
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
 

}

.items{
    margin-right: 1.4rem;
    font-family: 'PT Mono', monospace;
  transition: all 0.5s ease;
   
    cursor: pointer;
}

.items:hover{

    color:#0c91c2; ;
    border-bottom: 1px solid #0c91c2;
   

}




 #connect{

    background-color:rgb(19 15 73);
    color:white;
  
   padding-top: 10px;
   padding-bottom: 10px;
   padding-left: 30px;
   padding-right: 30px;
   border-radius:5px;
  

}

#connect:hover{
    color: white;
}


.btnmenubar{
   
    display: none;
    right: 10px;
    top: 12px;
    padding: 5px;
    color:rgb(19 15 73);
    font-size: 25px;
}


@media screen and (max-width:700px) {


    .logo{
        color:rgb(19 15 73);
        font-family: Gill Sans,Gill Sans MT,Calibri,Trebuchet MS,sans-serif;

        font-weight: 600;
        cursor: pointer;
        margin-left: 0;
       
        
   
        
    }

    .list{
        flex-direction: column;
        
       position: fixed;
       top: 3.5rem;
       left: 0;
      width: 100%;
      height: 100vh;
     display: block;
     text-align: center;
    
    
        box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
        
        background-color: white;
        
        
       
        
        

    }


   

   

    .items{


        margin: 2.5rem 0;
        font-size: 1.5rem;
        

        
       
    }

    

    .btnmenubar{

        display: block;
       
    }
    
}




